:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --gray: hsl(0, 0%, 90%);
  --light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-bold: 700;
  --font-regular: 500;
  --font-light: 400;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  margin-bottom: 0;
}

p {
  margin-top: 5px;
  opacity: 50%;
  font-weight: var(--font-regular);
  font-size: 1.1rem;
  line-height: 1.9rem;
}

button {
  border: none;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
  padding: 20px 30px;
}

.button-smaller {
  padding-inline: 40px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: white;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}

.button-smaller:hover {
  background: var(--dark-cyan);
}

section {
  margin-inline: 50px;
}

@media (max-width: 550px) {
  section {
    margin-inline: 20px;
  }
  .back-project-popup {
    right: 20px !important;
    left: 20px !important;
  }
}
nav {
  height: 550px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 50px;
}
nav .nav__container {
  margin-left: 200px;
  margin-right: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1350px) {
  nav .nav__container {
    margin-left: 100px;
    margin-right: 100px;
  }
}
@media (max-width: 1100px) {
  nav .nav__container {
    margin-left: 40px;
    margin-right: 40px;
  }
}
nav .nav__container .hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 31px;
  height: 21px;
  cursor: pointer;
}
nav .nav__container .hamburger .bar {
  width: 100%;
  height: 3px;
  background-color: white;
  border-radius: 3px;
  transition: transform 500ms;
}
nav .nav__container .options-container {
  display: flex;
  list-style: none;
}
nav .nav__container .options-container li a {
  color: white;
  margin-left: 2rem;
}
@media (max-width: 640px) {
  nav .nav__container .hamburger {
    display: flex;
  }
  nav .nav__container .options-container {
    display: none;
  }
  nav .nav__container div.active {
    z-index: 100;
    position: relative;
    top: 5px;
  }
  nav .nav__container div.active :nth-child(1) {
    position: relative;
    transform: rotate(45deg);
  }
  nav .nav__container div.active :nth-child(2) {
    position: relative;
    top: -17px;
    transform: rotate(-45deg);
  }
  nav .nav__container div.active :nth-child(3) {
    display: none;
  }
  nav .nav__container .options-container.active {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    z-index: 100;
    top: 100px;
    left: 0;
    right: 0;
    height: 250px;
    text-align: left;
    margin-left: 40px;
    margin-right: 40px;
    background-color: white;
    padding: 0;
    font-size: 1.3rem;
    font-weight: var(--font-regular);
    border-radius: 5px;
  }
  nav .nav__container .options-container.active li {
    margin: 0;
    height: 100%;
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--light-gray);
  }
  nav .nav__container .options-container.active li:nth-of-type(3) {
    border: none;
  }
  nav .nav__container .options-container.active li .nav__item {
    padding-left: 30px;
    color: var(--black);
    margin: 0;
  }
}

